*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container{
  max-width: 1230px;
  padding: 0 15px;
  margin: auto;
}
body{
  display: flex;flex-direction: column;background: #000;color: aliceblue;height: 100vh;text-align: center;
 
}
.bmiBox{
  display: flex;flex-direction: column;row-gap: 20px;
}
.inputs{
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
}
input{
  padding: 5px 7px;
  border: 1px solid #ffffff7f;
  outline: none;
  border-radius: 5px;background: local;
  color: #ffffffb8;
}
.render-box{
  display: flex;
  flex-direction: column;
  row-gap: 7px;

}
.item{
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 30px;
  padding: 20px 0px;
  border: 2px solid #fff;
  border-radius: 10px;

}
button{
  padding: 7px 10px;
  outline: none;cursor: pointer;
  color: #fff;
  border-radius: 5px;
  border: none;
}


.add-btn{
  background: #1a07e7;
}
.edit-btn{
  background: green;
}
.delete-btn{
  background: red;
}